.Header-link{
    padding: 10px;
    color: rgb(244,218,159);
    font-weight: 600;
    font-size: 20px;
    text-decoration: none;
}
.Header-link:hover{
    color: rgb(255, 123, 0);
    
}

.header{
    position: fixed;
    top: 0;
    width: 100%;
    padding: 10px 40px;
    background-color: rgba(80, 29, 7, 0.986);
    box-shadow: 0px 10px 20px 5px #504427;
}
@media screen and (max-width: 760px) {
    .header {
        text-align: center;
        padding: 35px 40px 20px;
    }
  }